<template>
  <div class="main-component row">
    <PageHeader :title="'Horários - Cursos'">
      <BaseButton
        title="Filtros"
        :type="'icon'"
        :color="'gray'"
        @click="toggleAsideModal('filtros')"
      >
        <font-awesome-icon :icon="['fas', 'list-ul']" />
      </BaseButton>

      <BaseButton
        title="Relátorio"
        :type="'icon'"
        :color="'gray'"
        @click="toggleAsideModal('relatorio')"
      >
        <font-awesome-icon :icon="['fas', 'file-alt']" />
      </BaseButton>

      <BaseButton
        title="Ajuda"
        :type="'icon'"
        :color="'lightblue'"
        @click="toggleAsideModal('ajuda')"
      >
        <font-awesome-icon :icon="['fas', 'question']" />
      </BaseButton>
    </PageHeader>

    <div
      v-show="!onLoading.table && hasAnyHorariosActived"
      class="row w-100 m-0"
    >
      <div v-show="semestre1IsActived" class="w-100">
        <h2 class="semestre-title">1º SEMESTRE</h2>

        <ListTableHorarios
          v-for="curso in CursosInHorariosFiltred"
          :key="curso.codigo + curso.periodoInicial1Semestre"
          :curso="{
            id: curso.id,
            nome: curso.nome,
            turno: curso.turno,
            periodoInicial: curso.periodoInicial1Semestre,
            horarios: curso.horarios1Semestre,
          }"
        />

        <template v-if="EletivasIsActived">
          <h3 class="curso-title pl-1">Eletivas</h3>
          <TableEletivas :TurmasEletivas="horariosAtivos1.Eletivas" />
        </template>
      </div>

      <div v-show="semestre2IsActived" class="w-100">
        <h2 class="semestre-title">2º SEMESTRE</h2>

        <ListTableHorarios
          v-for="curso in CursosInHorariosFiltred"
          :key="curso.nome + curso.periodoInicial2Semestre"
          :curso="{
            id: curso.id,
            nome: curso.nome,
            turno: curso.turno,
            periodoInicial: curso.periodoInicial2Semestre,
            horarios: curso.horarios2Semestre,
          }"
        />

        <template v-if="EletivasIsActived">
          <h3 class="curso-title pl-1">Eletivas</h3>
          <TableEletivas :TurmasEletivas="horariosAtivos2.Eletivas" />
        </template>
      </div>
    </div>

    <p
      v-if="
        (!this.semestre1IsActived && !this.semestre2IsActived) ||
          !hasAnyHorariosActived
      "
      class="text-empty"
    >
      <b>Nenhum horário encontrado.</b> Clique no botão de filtros
      <font-awesome-icon :icon="['fas', 'list-ul']" class="mx-1" />para
      selecioná-los.
    </p>

    <ModalFiltros
      ref="modalFiltros"
      :callbacks="modalFiltrosCallbacks"
      :tabsOptions="modalFiltrosTabs"
    >
      <div class="div-table">
        <BaseTable
          v-show="modalFiltrosTabs.current === 'Cursos'"
          :type="'modal'"
        >
          <template #thead>
            <th style="width: 25px"></th>
            <th
              class="clickable t-start"
              style="width: 50px"
              @click="toggleOrder(ordemCursos, 'codigo')"
            >
              Cód.
              <i :class="setIconByOrder(ordemCursos, 'codigo')"></i>
            </th>
            <th
              class="clickable t-start"
              style="width: 375px;"
              @click="toggleOrder(ordemCursos, 'nome')"
            >
              Nome
              <i :class="setIconByOrder(ordemCursos, 'nome')"></i>
            </th>
          </template>
          <template #tbody>
            <tr
              v-for="curso in CursosModalOrdered"
              :key="'MdCurso' + curso.codigo + curso.id"
              @click="toggleItemInArray(curso, filtroCursos.selecionados)"
            >
              <td style="width: 25px">
                <input
                  type="checkbox"
                  :value="curso"
                  v-model="filtroCursos.selecionados"
                  class="form-check-input position-static m-0"
                />
              </td>
              <td style="width: 50px">{{ curso.codigo }}</td>
              <td style="width: 375px" class="t-start">{{ curso.nome }}</td>
            </tr>
          </template>
        </BaseTable>

        <BaseTable
          v-show="modalFiltrosTabs.current === 'Semestres'"
          :type="'modal'"
        >
          <template #thead>
            <th style="width: 25px"></th>
            <th style="width: 425px" class="t-start">Semestre Letivo</th>
          </template>
          <template #tbody>
            <tr @click="filtroSemestres.primeiro = !filtroSemestres.primeiro">
              <td style="width: 25px">
                <input
                  type="checkbox"
                  class="form-check-input position-static m-0"
                  v-model="filtroSemestres.primeiro"
                />
              </td>
              <td style="width: 425px" class="t-start">PRIMEIRO</td>
            </tr>
            <tr @click="filtroSemestres.segundo = !filtroSemestres.segundo">
              <td style="width: 25px">
                <input
                  type="checkbox"
                  class="form-check-input position-static m-0"
                  v-model="filtroSemestres.segundo"
                />
              </td>
              <td style="width: 425px" class="t-start">SEGUNDO</td>
            </tr>
          </template>
        </BaseTable>
      </div>
    </ModalFiltros>

    <ModalRelatorio ref="modalRelatorio" @selection-option="pdf($event)" />

    <ModalAjuda ref="modalAjuda">
      <li class="list-group-item">
        <b>Visualizar grade:</b>
        Clique no ícone filtros
        <font-awesome-icon :icon="['fas', 'list-ul']" class="icon-gray" />. Em
        seguida, utilize as abas para navegar entre os filtros. Selecione as
        informações que deseja visualizar e clique no botão OK
      </li>
      <li class="list-group-item">
        <b>Relatório:</b> Clique no ícone relatório
        <font-awesome-icon :icon="['fas', 'file-alt']" class="icon-gray" />. Em
        seguida, indique se deseja gerar o relatório completo com a grade
        completa de todos os cursos ou o relatório parcial com as informações
        exibidas na tela.
      </li>
    </ModalAjuda>
  </div>
</template>

<script>
import { mapGetters } from "vuex";
import {
  toggleItemInArray,
  toggleOrdination,
  toggleAsideModal,
} from "@/common/mixins";
import { ModalRelatorio, ModalAjuda, ModalFiltros } from "@/components/modals";
import TableEletivas from "./TableEletivas.vue";
import ListTableHorarios from "./ListTableHorarios.vue";

export default {
  name: "DashboardHorarios",
  mixins: [toggleItemInArray, toggleOrdination, toggleAsideModal],
  components: {
    ModalFiltros,
    ModalAjuda,
    ModalRelatorio,
    ListTableHorarios,
    TableEletivas,
  },
  data() {
    return {
      asideModalsRefs: ["modalFiltros", "modalAjuda", "modalRelatorio"],
      ordemCursos: { order: "codigo", type: "asc" },
      listaDePeriodos: [],
      filtroCursos: {
        selecionados: [],
        ativados: [],
      },
      filtroSemestres: {
        primeiro: true,
        segundo: true,
        ativo: 3,
      },
      horariosAtivos1: {
        CCD: [[], [], [], [], [], [], [], [], [], []],
        CCN: [[], [], [], [], [], [], [], [], [], []],
        EC: [[], [], [], [], [], [], [], [], [], []],
        SI: [[], [], [], [], [], [], [], [], [], []],
        Eletivas: [],
      },
      horariosAtivos2: {
        CCD: [[], [], [], [], [], [], [], [], [], []],
        CCN: [[], [], [], [], [], [], [], [], [], []],
        EC: [[], [], [], [], [], [], [], [], [], []],
        SI: [[], [], [], [], [], [], [], [], [], []],
        Eletivas: [],
      },
      modalFiltrosTabs: {
        current: "Cursos",
        array: ["Cursos", "Semestres"],
      },
      modalFiltrosCallbacks: {
        selectAll: {
          Cursos: () => {
            this.filtroCursos.selecionados = [...this.OptionsCursosDCC];
          },
          Semestres: () => {
            this.filtroSemestres.primeiro = true;
            this.filtroSemestres.segundo = true;
          },
        },
        selectNone: {
          Cursos: () => {
            this.filtroCursos.selecionados = [];
          },
          Semestres: () => {
            this.filtroSemestres.primeiro = false;
            this.filtroSemestres.segundo = false;
          },
        },
        btnOk: () => {
          this.setSemestreAtivo();
          this.filtroCursos.ativados = [...this.filtroCursos.selecionados];
        },
      },
    };
  },

  beforeMount() {
    this.currentPlano = this.$_.find(this.$store.state.plano.Plano, {
      id: parseInt(localStorage.getItem("Plano")),
    });
    for (let i = 1; i < 5; i++) {
      this.createHorarios(i, 1);
      this.createHorarios(i, 2);
    }

    this.createHorarioEletivas(1);
    this.createHorarioEletivas(2);

    this.modalFiltrosCallbacks.selectAll.Cursos();
    this.filtroCursos.ativados = [...this.filtroCursos.selecionados];
  },

  methods: {
    setSemestreAtivo() {
      const { primeiro, segundo } = this.filtroSemestres;

      if (primeiro && !segundo) this.filtroSemestres.ativo = 1;
      else if (!primeiro && segundo) this.filtroSemestres.ativo = 2;
      else if (primeiro && segundo) this.filtroSemestres.ativo = 3;
      else this.filtroSemestres.ativo = undefined;
    },
    isEven(number) {
      if (number % 2 === 0) return true;
      else return false;
    },

    checkTurmaHorario(turma, horario) {
      if (turma.Horario1 == horario || turma.Horario2 == horario) {
        return true;
      } else return false;
    },
    checkPeriodo(turma, periodo) {
      for (var g = 0; g < this.DisciplinaGrades.length; g++) {
        if (
          this.DisciplinaGrades[g].Disciplina == turma &&
          this.DisciplinaGrades[g].periodo == periodo
        ) {
          return true;
        }
      }
    },
    updateHorarios() {
      for (let i = 0; i < 10; i++) {
        for (let j = 0; j < this.horariosAtivos1.CCD[i].length; j++)
          for (let k = 0; k < this.AllTurmas.length; k++) {
            if (this.horariosAtivos1.CCD[i][j].id == this.AllTurmas[k].id) {
              this.horariosAtivos1.CCD[i].splice(j, 1, this.AllTurmas[k]);
            }
          }
        for (let j = 0; j < this.horariosAtivos1.CCN[i].length; j++)
          for (let k = 0; k < this.AllTurmas.length; k++) {
            if (this.horariosAtivos1.CCN[i][j].id == this.AllTurmas[k].id) {
              this.horariosAtivos1.CCN[i].splice(j, 1, this.AllTurmas[k]);
            }
          }
        for (let j = 0; j < this.horariosAtivos1.EC[i].length; j++)
          for (let k = 0; k < this.AllTurmas.length; k++) {
            if (this.horariosAtivos1.EC[i][j].id == this.AllTurmas[k].id) {
              this.horariosAtivos1.EC[i].splice(j, 1, this.AllTurmas[k]);
            }
          }
        for (let j = 0; j < this.horariosAtivos1.SI[i].length; j++)
          for (let k = 0; k < this.AllTurmas.length; k++) {
            if (this.horariosAtivos1.SI[i][j].id == this.AllTurmas[k].id) {
              this.horariosAtivos1.SI[i].splice(j, 1, this.AllTurmas[k]);
            }
          }
      }

      this.$store.commit("redefinirAtivas1", {
        Ativas: this.horariosAtivos1,
      });

      for (let i = 0; i < 10; i++) {
        for (let j = 0; j < this.horariosAtivos2.CCD[i].length; j++)
          for (let k = 0; k < this.AllTurmas.length; k++) {
            if (this.horariosAtivos2.CCD[i][j].id == this.AllTurmas[k].id) {
              this.horariosAtivos2.CCD[i].splice(j, 1, this.AllTurmas[k]);
            }
          }
        for (let j = 0; j < this.horariosAtivos2.CCN[i].length; j++)
          for (let k = 0; k < this.AllTurmas.length; k++) {
            if (this.horariosAtivos2.CCN[i][j].id == this.AllTurmas[k].id) {
              this.horariosAtivos2.CCN[i].splice(j, 1, this.AllTurmas[k]);
            }
          }
        for (let j = 0; j < this.horariosAtivos2.EC[i].length; j++)
          for (let k = 0; k < this.AllTurmas.length; k++) {
            if (this.horariosAtivos2.EC[i][j].id == this.AllTurmas[k].id) {
              this.horariosAtivos2.EC[i].splice(j, 1, this.AllTurmas[k]);
            }
          }
        for (let j = 0; j < this.horariosAtivos2.SI[i].length; j++)
          for (let k = 0; k < this.AllTurmas.length; k++) {
            if (this.horariosAtivos2.SI[i][j].id == this.AllTurmas[k].id) {
              this.horariosAtivos2.SI[i].splice(j, 1, this.AllTurmas[k]);
            }
          }
      }

      this.$store.commit("redefinirAtivas1", {
        Ativas2: this.horariosAtivos2,
      });

      this.$store.commit("redefinirAtivas2", {
        Ativas: this.horariosAtivos2,
      });
    },

    createHorarios(curso, semestre) {
      let horariosAtivos;
      if (semestre === 1) horariosAtivos = this.horariosAtivos1;
      else horariosAtivos = this.horariosAtivos2;
      switch (curso) {
        case 1:
          horariosAtivos = horariosAtivos.CCN;
          break;
        case 2:
          horariosAtivos = horariosAtivos.EC;
          break;
        case 3:
          horariosAtivos = horariosAtivos.SI;
          break;
        case 4:
          horariosAtivos = horariosAtivos.CCD;
          break;
        default:
          return;
      }

      let periodoInicio;
      if (semestre === 1) periodoInicio = 1;
      else periodoInicio = 3;

      let plano = this.$_.find(this.$store.state.plano.Plano, {
        id: parseInt(localStorage.getItem("Plano")),
      });

      let gradesCurso = this.$_.filter(this.AllGrades, { Curso: curso });
      let gradesAtivas = [];

      gradesCurso.forEach((g) => {
        let fim =
          1 +
          2 * (plano.ano - parseInt(g.periodoInicio.slice(0, 4), 10)) +
          (periodoInicio - parseInt(g.periodoInicio.slice(5, 6), 10)) / 2;
        if (fim > 10) {
          fim = 10;
        }
        gradesAtivas.push({ grade: g, fim: fim });
      });
      if (gradesAtivas[gradesAtivas.length - 1].fim !== 10) {
        gradesAtivas[gradesAtivas.length - 1].fim = 10;
      }

      let pedidos = [];
      for (let t in this.$store.state.pedido.Pedidos) {
        for (let pedido in this.$store.state.pedido.Pedidos[t]) {
          if (this.$store.state.pedido.Pedidos[t][pedido].Curso === curso) {
            pedidos.push(this.$store.state.pedido.Pedidos[t][pedido]);
          }
        }
      }
      let pedidosExternos = [];
      for (let t in this.$store.state.pedidoExterno.Pedidos) {
        for (let pedido in this.$store.state.pedidoExterno.Pedidos[t]) {
          if (
            this.$store.state.pedidoExterno.Pedidos[t][pedido].Curso === curso
          ) {
            pedidosExternos.push(
              this.$store.state.pedidoExterno.Pedidos[t][pedido]
            );
          }
        }
      }
      let even =
        this.$store.state.curso.Cursos[curso - 1].semestreInicial % 2 ===
        semestre - 1;
      let turmas = this.$_.filter(this.$store.state.turma.Turmas, {
        periodo: periodoInicio,
      });
      let turmasExternas = this.$_.filter(
        this.$store.state.turmaExterna.Turmas,
        {
          periodo: periodoInicio,
        }
      );
      let disciplinasGrades = this.DisciplinaGrades;
      let inicio = 0;

      for (let i = 0; inicio < 10 && i < gradesAtivas.length; i++) {
        let disciplinasGradeAtual = this.$_.filter(disciplinasGrades, {
          Grade: gradesAtivas[i].grade.id,
        });
        for (let j = 0; j < disciplinasGradeAtual.length; j++) {
          if (
            disciplinasGradeAtual[j].periodo <= gradesAtivas[i].fim &&
            disciplinasGradeAtual[j].periodo > inicio &&
            this.isEven(disciplinasGradeAtual[j].periodo) === even
          ) {
            for (let k = 0; k < turmas.length; k++) {
              if (disciplinasGradeAtual[j].Disciplina == turmas[k].Disciplina) {
                let p = this.$_.find(pedidos, { Turma: turmas[k].id });
                if (p.vagasPeriodizadas > 0) {
                  horariosAtivos[disciplinasGradeAtual[j].periodo - 1].push(
                    turmas[k]
                  );
                }
              }
            }
            for (let k = 0; k < turmasExternas.length; k++) {
              if (
                disciplinasGradeAtual[j].Disciplina ==
                turmasExternas[k].Disciplina
              ) {
                let p = this.$_.find(pedidosExternos, {
                  Turma: turmasExternas[k].id,
                });
                if (p.vagasPeriodizadas > 0) {
                  horariosAtivos[disciplinasGradeAtual[j].periodo - 1].push(
                    turmasExternas[k]
                  );
                }
              }
            }
          }
        }
        inicio = gradesAtivas[i].fim;
      }
    },
    createHorarioEletivas(semestre) {
      let horariosAtivos;
      if (semestre === 1) horariosAtivos = this.horariosAtivos1.Eletivas;
      else horariosAtivos = this.horariosAtivos2.Eletivas;

      let periodoInicio;
      if (semestre === 1) periodoInicio = 1;
      else periodoInicio = 3;

      let turmas = this.$_.filter(this.$store.state.turma.Turmas, {
        periodo: periodoInicio,
      });

      let plano = this.$_.find(this.$store.state.plano.Plano, {
        id: parseInt(localStorage.getItem("Plano")),
      });

      let disciplinasGrades = this.DisciplinaGrades;
      let gradesAtivas = [[], [], [], []];

      for (let i = 1; i < 5; i++) {
        let gradesCurso = this.$_.filter(this.AllGrades, { Curso: i });
        let inicio = 0;
        gradesCurso.forEach((g) => {
          let fim =
            1 +
            2 * (plano.ano - parseInt(g.periodoInicio.slice(0, 4), 10)) +
            (periodoInicio - parseInt(g.periodoInicio.slice(5, 6), 10)) / 2;
          if (fim > 10) {
            fim = 10;
          }
          gradesAtivas[i - 1].push({ grade: g, fim: fim, inicio: inicio });
          inicio = fim;
        });
        if (gradesAtivas[i - 1][gradesAtivas[i - 1].length - 1].fim !== 10) {
          gradesAtivas[i - 1][gradesAtivas[i - 1].length - 1].fim = 10;
        }
      }
      let eletiva = true;
      for (let i = 0; i < turmas.length; i++) {
        for (let j = 0; j < 4; j++) {
          for (let k = 0; k < gradesAtivas[j].length; k++) {
            let disciplinasGradeAtual = this.$_.filter(disciplinasGrades, {
              Grade: gradesAtivas[j][k].grade.id,
            });
            let disciplinaGrade = this.$_.find(disciplinasGradeAtual, {
              Disciplina: turmas[i].Disciplina,
            });
            if (
              disciplinaGrade &&
              disciplinaGrade.periodo < gradesAtivas[j][k].fim &&
              disciplinaGrade.periodo >= gradesAtivas[j][k].inicio
            )
              for (let l = 0; l < disciplinasGradeAtual.length; l++) {
                if (turmas[i].Disciplina == disciplinasGradeAtual[l].Disciplina)
                  eletiva = false;
              }
          }
        }
        if (eletiva) {
          horariosAtivos.push(turmas[i]);
        }
        eletiva = true;
      }
    },
    pdf(completo) {
      var pdfMake = require("pdfmake/build/pdfmake.js");
      if (pdfMake.vfs == undefined) {
        var pdfFonts = require("pdfmake/build/vfs_fonts.js");
        pdfMake.vfs = pdfFonts.pdfMake.vfs;
      }
      var tables = [];
      var disciplinas = this.AllDisciplinas;
      var periodosCCD1 = this.horariosAtivos1.CCD;
      var periodosCCN1 = this.horariosAtivos1.CCN;
      var periodosEC1 = this.horariosAtivos1.EC;
      var periodosSI1 = this.horariosAtivos1.SI;
      var eletivas1 = this.horariosAtivos1.Eletivas;
      var seg = "",
        ter = "",
        qua = "",
        qui = "",
        sex = "";
      var vazio = 0;
      tables.push({
        columns: [
          {
            image:
              "data:image/png;base64,",
            fit: [60, 60],
            alignment: "left",
            width: "16%",
            margin: [0, 0, 0, 10],
          },
          [
            {
              text:
                "Horários dos cursos do Departamento de Ciência da Computação",
              alignment: "center",
              bold: true,
              fontSize: 10,
            },
            {
              text: "1º Semestre " + this.currentPlano.ano + " - " + this.currentPlano.nome,
              alignment: "center",
              bold: true,
              fontSize: 10,
              margin: [0, 0, 0, 5],
            },
          ],
          {
            image:
              "data:image/png;base64,",
            fit: [60, 60],
            alignment: "right",
            width: "16%",
            margin: [0, 0, 0, 10],
          },
        ],
      });
      if (
        this.$_.find(this.filtroCursos.ativados, { codigo: "65C" }) ||
        completo
      ) {
        tables.push({
          text: "Ciência da Computação - integral",
          bold: true,
          margin: [0, 10, 0, 5],
          fontSize: 10,
        });

        for (let i = 0; i < 10; i++) {
          if (periodosCCD1[i].length === 0) {
            vazio = vazio + 1;
          } else {
            tables.push({
              text: i + 1 + "º Período",
              fontSize: 8,
              bold: true,
              margin: [0, 5, 0, 5],
            });
            let tableHorariosBody = [
              [
                {
                  text: "Horário",
                  alignment: "center",
                  fontSize: 8,
                  bold: true,
                },
                {
                  text: "Segunda",
                  alignment: "center",
                  fontSize: 8,
                  bold: true,
                },
                {
                  text: "Terça",
                  alignment: "center",
                  fontSize: 8,
                  bold: true,
                },
                {
                  text: "Quarta",
                  alignment: "center",
                  fontSize: 8,
                  bold: true,
                },
                {
                  text: "Quinta",
                  alignment: "center",
                  fontSize: 8,
                  bold: true,
                },
                {
                  text: "Sexta",
                  alignment: "center",
                  fontSize: 8,
                  bold: true,
                },
              ],
            ];
            for (let d = 0; d < 4; d++) {
              for (let j = 0; j < periodosCCD1[i].length; j++) {
                if (this.checkTurmaHorario(periodosCCD1[i][j], 1 + d)) {
                  for (let k = 0; k < disciplinas.length; k++) {
                    if (periodosCCD1[i][j].Disciplina === disciplinas[k].id) {
                      if (seg !== "") seg = seg + "\n";
                      seg =
                        seg +
                        disciplinas[k].codigo +
                        " " +
                        periodosCCD1[i][j].letra;
                    }
                  }
                }
